.App {
    text-align: center;
  }
  
  .App-logo {
    height: 40vmin;
    pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo-spin infinite 20s linear;
    }
  }
  
  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .App-link {
    color: #61dafb;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  
  .blank{
    color: green;
  }
  
  .pretext{
   border: none;
   outline: none;
  }
  
  #outlinetext{
    border: none;
    outline: none;
  
  }
  
  h2{
    display: flex;
    
  }
  
  h2 div{
    position: absolute;
    right: 20vw;
  }
  
  #c1{
    color: green;
    background-color: green;
  
  }